import React, { createContext, useState, useEffect } from 'react';
import { checkAuthStatus, logoutUser } from '../api/api'; // Import the function

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [username, setUsername] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const verifyAuth = async () => {
      try {
        const { isLoggedIn, username } = await checkAuthStatus();
        setIsLoggedIn(isLoggedIn);
        if (isLoggedIn) {
          setUsername(username);
        }
      } catch (error) {
        console.error('Error checking authentication status:', error);
      } finally {
        setIsLoading(false);
      }
    };

    verifyAuth();
  }, []);

  const login = (username) => {
    setIsLoggedIn(true);
    setUsername(username);
  };

  const logout = async () => {
    try {
      await logoutUser(username); // Pass the username to the logoutUser API function
      setIsLoggedIn(false);
      setUsername('');
    } catch (error) {
      console.error('Error logging out:', error);
    }
  };

  return (
    <AuthContext.Provider value={{ isLoggedIn, isLoading, username, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};
